.myContainer {
  @apply grid grid-cols-2 min-h-screen justify-items-center items-center
  h-screen overflow-hidden relative text-white lg:grid-cols-1;
}

.gif_box {
  @apply h-full w-full bg-[#121212] flex items-center min-h-full
  justify-center flex-col max-h-screen overflow-hidden py-40 pt-[14rem]
  lg:hidden;
}

.desc {
  @apply bg-white px-5 py-3 rounded-[10px] max-w-[400px];
}

.form {
  @apply flex relative justify-center flex-col max-w-[25rem] mx-auto gap-5 
  w-full px-5 h-full max-h-[900px] pt-[4rem];
}

.logo {
  @apply w-[12rem] h-12 absolute top-3 cursor-pointer;
}
