@tailwind base;
@tailwind components;
@tailwind utilities;

html {
  @apply bg-[var(--main-html-bg)] text-[var(--white)] max-w-[100vw] w-full overflow-x-hidden min-h-screen
    2xl:text-[18px] 
    xl:text-[16px]
    lg:text-[15px]
    md:text-[14px]
    sm:text-[13px]
    xs:text-[12px];
}
main {
  @apply py-10;
}

:root {
  --input-px: 1.5rem;
  --input-py: 0.9rem;
  --dur: 300ms;
  --max-w: 1392px;
  --main-py: 5rem;

  --main-html-bg: #1a1c1e;
  --black: #1b212d;
  --green: #267365;
  --green-light: #267365b3;
  --white: #fff;
  --gray: #2e3134;
  --gray-light: #62686e;
  --tw-ring-color: var(--green) !important;
}
@media (max-width: 1024px) {
  :root {
    --main-py: 3rem;
  }
}

* {
  @apply text-inherit;
  text-decoration-skip-ink: none;
  font-family: "Noto Sans Armenian", sans-serif;
  font-family: "Roboto", sans-serif;
}

#root {
  @apply min-h-screen overflow-x-hidden flex flex-col;
}
main {
  @apply flex-1;
}

.container {
  @apply mx-auto px-[120px] max-w-[var(--max-w)]
  xl:px-[50px]
  md:!px-[24px];
}
.underline {
  content: "";
  position: "absolute";
  right: "0";
  left: "0";
  bottom: "-5px";
  width: "100%";
  height: "4px";
  background: "linear-gradient(90deg, #267365b3 0%, #267365 100.84%)";
  border-radius: "3px";
}

.main-input {
  @apply px-[--input-px] p-[var(--input-py)_var(--input-px)] w-full rounded-[5px]
  duration-[--dur] text-base text-[var(--green)] focus:ring-[var(--green)]
  !border-[var(--green)] relative bg-transparent border 
  !border-[#6d7277] text-left shadow-md sm:text-sm !ring-transparent 
  !outline-none !text-white;
  box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.05);
  transition-property: padding;
}
.main-input::placeholder {
  @apply opacity-0 text-base;
}
.main-input.show {
  @apply pb-[calc(var(--input-py)_*_0.5)] pt-[calc(var(--input-py)_*_1.5)];
}
/* .main-input.my-outline {
  @apply relative w-full cursor-default rounded-lg 
  bg-transparent border !border-[#6d7277] text-left 
  shadow-md sm:text-sm !ring-transparent !outline-none !text-white;
} */
.main-input-placeholder {
  @apply absolute left-[--input-px] -translate-y-1/2 
  text-[#78778B] duration-100 text-base z-[1];
  user-select: none;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-background-clip: text;
  -webkit-text-fill-color: white;
  -webkit-box-shadow: 0 0 0 30px var(--main-html-bg) inset !important;
}
::-webkit-scrollbar {
  display: none;
}

/* ###################################################################### */
/* ############################# Swiper ################################# */
/* ###################################################################### */
.swiper-pagination-bullet {
  @apply w-[22px] h-0.5 opacity-100 rounded-none border-[none] bg-[#37363e];
  -webkit-transition: background-color 0.3s ease-in-out, width 0.3s ease-in-out;
  -o-transition: background-color 0.3s ease-in-out, width 0.3s ease-in-out;
  transition: background-color 0.3s ease-in-out, width 0.3s ease-in-out;
}
.swiper-pagination {
  position: static;
}
.swiper {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.swiper-pagination-bullet-active {
  background-color: #2ab883;
  width: 30px px;
  height: 2px;
}
